import React, { useState } from 'react';

import grid from './data/grid.json';

type Feature = {
    name: string,
    desc: string,
    color: string
} & (
        { rect: [number, number, number, number] } |
        { cells: Array<[number, number]> }
    );

type FeatureMap = { [key: string]: Array<Feature> };

// import as an "any" to get around some typechecking
const gridDefs = require('./data/gridDefs.json');
const allFeatures = require('./data/features.json') as FeatureMap;

type Props = {
    rows: number,
    cols: number,
    tileSize: number
}

export default function MapGrid(props: Props) {
    const { rows, cols, tileSize } = props;

    const [focus, setFocus] = useState<[number, number] | null>(null);
    const [featuredTile, setFeaturedTile] = useState<[number, number] | null>(null);
    const focusChar = focus == null ? '.' : grid[focus[0]].charAt(focus[1]);

    const featureKey = featuredTile && `${featuredTile[0]}-${featuredTile[1]}`;

    const focusTile = gridDefs[focusChar];
    const features = featureKey && allFeatures[featureKey];

    function makeRects(rows: number, cols: number, size: number, focusChar: string) {
        const out = [];
        for (let row = 0; row < rows; row++) {
            for (let col = 0; col < cols; col++) {
                const ch = grid[row][col];
                out.push(
                    <g>
                        <rect
                            x={col * size}
                            y={row * size}
                            width={size}
                            height={size}
                            onMouseOver={() => setFocus([row, col])}
                            onClick={() => setFeaturedTile([row, col])}
                            style={{
                                fill: gridDefs[ch].color,
                                stroke: 'rgba(0, 0, 0)',
                            }}
                        />
                        {focusChar === ch && focusChar !== '.' && focusChar !== '_' && < rect
                            x={col * size}
                            y={row * size}
                            width={size}
                            height={size}
                            style={{
                                fill: 'white',
                                pointerEvents: 'none'
                            }}
                            opacity={0.5}
                        />}
                    </g>
                )
            }
        }
        return out;
    }

    let rects = makeRects(rows, cols, tileSize, focusChar);

    return (
        <div style={{ display: 'flex', flexDirection: 'row' }}>
            <svg
                height={rows * tileSize}
                width={cols * tileSize}
                style={
                    {
                        border: '1px solid white',
                        margin: '10px'
                    }
                }
                onMouseLeave={() => setFocus(null)}
            >
                {rects}
                {
                    featuredTile != null && <rect
                        x={featuredTile[1] * tileSize}
                        y={featuredTile[0] * tileSize}
                        width={tileSize}
                        height={tileSize}
                        stroke={'white'}
                        strokeWidth='3px'
                        fill='transparent'
                    />
                }
            </svg>
            <div style={{ flexShrink: 1, display: 'flex', flexDirection: 'column' }}>
                <svg
                    height={256}
                    width={256}
                    style={{
                        marginTop: '10px',
                        border: '1px solid yellow'
                    }}
                >
                    {
                        [...new Array(15)].map((_, n) => (
                            <g>
                                <line
                                    x1={0} y1={n * 16 + 16}
                                    x2={256} y2={n * 16 + 16}
                                    stroke='#555'
                                />
                                <line
                                    y1={0} x1={n * 16 + 16}
                                    y2={256} x2={n * 16 + 16}
                                    stroke='#555'
                                />
                            </g>
                        ))
                    }
                    {
                        (features != null && typeof features !== 'string') && features.map(
                            (feature) => {
                                if ('rect' in feature) {
                                    const [x1, y1, x2, y2] = feature.rect;
                                    const rectProps = {
                                        x: x1 * 16,
                                        y: y1 * 16,
                                        width: (x2 - x1) * 16,
                                        height: (y2 - y1) * 16
                                    }
                                    const textStyle = {
                                        fontSize: '12px',
                                        fontFamily: 'Raleway',
                                        fontStyle: 'italic',
                                        fontWeight: 700,
                                        textAnchor: 'middle' as 'middle',
                                        textAlign: 'center' as 'center',
                                        dominantBaseline: 'middle' as 'middle'
                                    };
                                    return <g>
                                        <rect
                                            {...rectProps}
                                            style={{
                                                fill: feature.color
                                            }}
                                            opacity={0.4}
                                        />
                                        <rect
                                            {...rectProps}
                                            style={{
                                                stroke: feature.color,
                                                fill: 'transparent'
                                            }}
                                        />
                                        <text
                                            x={(x1 + x2) * 8}
                                            y={(y1 + y2) * 8}
                                            style={{
                                                ...textStyle,
                                                stroke: 'black',
                                                strokeWidth: '4px'
                                            }}
                                        >
                                            {feature.name}
                                        </text>
                                        <text
                                            x={(x1 + x2) * 8}
                                            y={(y1 + y2) * 8}
                                            style={{
                                                ...textStyle,
                                                fill: 'white'
                                            }}
                                        >
                                            {feature.name}
                                        </text>
                                    </g>
                                } else {
                                    const { cells } = feature;
                                    return <g>
                                        {cells.map(
                                            ([x, y]) => (
                                                <rect
                                                    x1={x * 16}
                                                    x2={x * 16 + 16}
                                                    y1={y * 16}
                                                    y2={y * 16 + 16}
                                                    style={{
                                                        fill: feature.color
                                                    }}
                                                />
                                            ))
                                        }
                                    </g>
                                }
                            }
                        )
                    }
                </svg>
                <div style={
                    {
                        fontSize: '18px',
                        fontFamily: 'Raleway',
                        fontWeight: 300,
                        color: 'white',
                        margin: '10px',
                        maxWidth: '256px'
                    }
                }>
                    {focus == null ? 'Hover over the map' : focusTile.name}
                </div>
                <div style={
                    {
                        fontSize: '14px',
                        fontFamily: 'Raleway',
                        fontWeight: 300,
                        color: 'white',
                        margin: '0px 10px 10px 10px',
                        maxWidth: '256px'
                    }
                }>
                    {focus == null ? ' ' : focusTile.description}
                </div>
            </div>

        </div>
    );
}