function species(): string {
    const special = Math.random() < 0.1 ? '!' : '';
    const base = choose(SPECIES);
    if (base === 'hybrid') return `${species()}+${species()}`
    if (base === 'arthropod') return special + choose(ARTHROPOD);
    if (base !== 'tetrapod') return special + base;
    const rank2 = special + choose(TETRAPOD);
    if (rank2 !== 'mammal') return special + rank2;
    return special + choose(MAMMAL);
}

function hasJob(age: number): boolean {
    if (age <= 15) return false;
    if (age <= 24) return Math.random() < 0.7;
    if (age <= 55) return Math.random() < 0.85;
    if (age <= 65) return Math.random() < 0.5;
    return false;
}

export function choose<T>(src: Index<T>): T {
    const sum = src
        .map(([_, n]) => n)
        .reduce((prev, curr) => prev + curr);

    let val = rint(sum);
    for (let i = 0; i < src.length; i++) {
        if (val < src[i][1]) {
            if (Array.isArray(src[i][0])) {
                const subChoice: Index<T> = src[i][0] as Index<T>;
                return choose(subChoice);
            } else return src[i][0] as T;
        }
        val -= src[i][1];
    }
    return src[src.length - 1][0] as T;
}

export function rint(max: number): number {
    return Math.floor(Math.random() * max);
}

export function rlog(base: number): number {
    return -1 - Math.floor(Math.log(Math.random()) / Math.log(base))
}

export function person(hSize: number = 1, spec: string | null = null, shape: string | null = null) {
    const age = (choose(AGES) as number) + rint(5);
    const thisSpec = spec == null ? species() : (Math.random() * hSize) > 1 ? spec : species();
    const thisShape = shape == null ? choose(SHAPES) : (Math.random() * hSize) > 1 ? shape : choose(SHAPES);

    const nVariants = rlog(6);
    const variants = [...new Array(nVariants)]
        .map((_) => ({ type: choose(VARIANTS), n: rlog(5) + 1 }))

    let head = 1, arms = 2, legs = 2, tail = 1, other = [];
    for (const v of variants) {
        const { type, n } = v;
        switch (type) {
            case '+head': head += n; break;
            case '+arms': arms += 2 * n; break;
            case '+legs': legs += 2 * n; break;
            case '+tail': tail += n; break;
            case '+other': other.push(choose(VARIANT_OTHER)); break;
            default:
                break;
        }
    }
    const vari = [
        `${head > 1 ? `${head} heads` : ''}`,
        `${arms > 2 ? `${arms} arms` : ''}`,
        `${legs > 2 ? `${legs} legs` : ''}`,
        `${tail > 1 ? `${tail} tails` : ''}`, ...other].filter((x) => x !== '').join(', ');

    return { age, sex: choose(SEXES), species: thisSpec, shape: thisShape, variants: vari, job: hasJob(age) ? choose(JOBS) : null }

}

export function household() {
    const size = choose(HSIZE) as number;
    const spec = species();
    const shape = choose(SHAPES);
    const out = [];

    for (let j = 0; j < size; j++) {
        out.push(person(size, spec, shape));
    }

    // sort descending by age
    out.sort((p1, p2) => p2.age - p1.age);

    return out;
}